import React, { useState } from 'react';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css'; 

 
 

const Locacionprecioautomobile = ({locacionPrecioAutomobileee}) => {
  const [preciolocacion, setpreciolocacion] = useState([2000, 50000 ]);

  return (
    <div className='card'>
      <label>
      Slectionnez le prix:
        <Slider
          min={2000}
          max={50000}
          step={50}
          range
          value={preciolocacion}
          onChange={(newRange) => {
            setpreciolocacion(newRange);
            locacionPrecioAutomobileee(newRange); // Llama a la funcin proporcionada por el prop
          }}
          trackStyle={{ backgroundColor: '#007bff', height: 10 }}
          handleStyle={{
            borderColor: '#007bff',
            height: 20,
            width: 20,
            marginLeft: -10,
            marginTop: -5,
            backgroundColor: '#007bff',
          }}
          railStyle={{ backgroundColor: '#ccc', height: 10 }}
        />

        <div style={{ marginTop: 10 }}>
        De {preciolocacion[0]}  {preciolocacion[1]} DA
        </div>
      </label>
    </div>
  );
};
 
export default Locacionprecioautomobile
